<!-- 添加、修改服务包 -->
<template>
  <div class="servicePackage-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb>
        <span slot="custom">{{ this.$route.query.id ? '编辑服务包' : '新增服务包' }}</span>
      </Breadcrumb>
    </div>
    <div class="inner-box">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px">
        <el-row>
          <el-col :span="9">
            <el-form-item label="服务包类型：" prop="packageType">
              <el-select placeholder="请选择服务包类型" v-model="ruleForm.packageType">
                <el-option
                  v-for="item in packageTypeOption"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  class="input-box"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label="服务包名称：" prop="packageName">
              <el-input
                v-model.trim="ruleForm.packageName"
                placeholder="请输入服务包名称"
                clearable
                :maxlength="50"
                class="input-box"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="9">
            <el-form-item label="服务包年龄限制：" class="flex">
              <el-form-item prop="limitAgeMin">
                <el-input
                  v-model.number="ruleForm.limitAgeMin"
                  placeholder="最小年龄"
                  clearable
                  :maxlength="NUMBERLEN"
                  class="small-box"
                  @change="handlervalidate('limitAgeMax')"
                ></el-input>
              </el-form-item>
              <span class="line">-</span>
              <el-form-item prop="limitAgeMax">
                <el-input
                  v-model.number="ruleForm.limitAgeMax"
                  placeholder="最大年龄"
                  clearable
                  :maxlength="NUMBERLEN"
                  class="small-box"
                ></el-input>
              </el-form-item>
              <span class="label-text">岁</span>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label="服务包性别限制：" prop="limitSexType">
              <el-select placeholder="请选择性别" v-model="ruleForm.limitSexType">
                <el-option
                  v-for="item in sexTypeOption"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  class="input-box"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="9">
            <el-form-item label="服务时长：" prop="serviceDays">
              <el-input
                v-model.number="ruleForm.serviceDays"
                placeholder="请输入服务时长"
                clearable
                :maxlength="NUMBERLEN"
                class="input-box"
              ></el-input>
              <span class="label-text">天</span>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label="服务包状态：" prop="packageStatus">
              <el-select
                placeholder="请选择状态"
                v-model="ruleForm.packageStatus"
                class="input-box"
              >
                <el-option
                  v-for="item in statusOption"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row v-if="ruleForm.packageType === 1">
          <el-col :span="9">
            <el-form-item label="服务包价格：" prop="originalPrice">
              <el-input
                v-model.trim="ruleForm.originalPrice"
                placeholder="请输入服务包价格"
                clearable
                :maxlength="NUMBERLEN"
                class="input-box"
                @change="handlervalidate('discountPrice')"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label="优惠价格：" prop="discountPrice">
              <el-input
                v-model.trim="ruleForm.discountPrice"
                placeholder="请输入优惠价格"
                clearable
                :maxlength="NUMBERLEN"
                class="input-box"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="服务包标签：" prop="labelIdList">
          <el-checkbox-group v-model="ruleForm.labelIdList">
            <el-checkbox v-for="(value, key) in labelListOption" :key="key" :label="key">{{
              value
            }}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="服务包项目信息：" prop="itemsJson">
          <serviceTable :dataInfo="ruleForm.itemsJson" ref="serviceTable"></serviceTable>
        </el-form-item>
      </el-form>
      <div class="btn-box">
        <el-button @click="handleBackList()">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import formRule from '../../../util/formRule' //表单验证
import serviceTable from './serviceTable' //服务项目动态表格
export default {
  name: 'servicePackage-add',
  components: {
    serviceTable
  },
  data() {
    var validateAge = (rule, value, callback) => {
      if (value !== '' || value === 0) {
        if (!this.ruleForm.limitAgeMin) {
          callback(new Error('请输入最小年龄'))
        } else {
          if (value < this.ruleForm.limitAgeMin) {
            callback(new Error('不能小于最小年龄'))
          } else {
            callback()
          }
        }
      } else {
        callback()
      }
    }
    var validatePrice = (rule, value, callback) => {
      if (value) {
        if (!this.ruleForm.originalPrice) {
          callback(new Error('请输入服务包价格'))
        } else {
          if (Number(value) > Number(this.ruleForm.originalPrice)) {
            callback(new Error('不能大于服务价格'))
          } else {
            callback()
          }
        }
      }
    }
    return {
      packageTypeOption: [
        {
          value: 1,
          label: '付费'
        },
        {
          value: 2,
          label: '免费'
        }
      ], //服务包类型
      sexTypeOption: [
        {
          value: 0,
          label: '不限制'
        },
        {
          value: 1,
          label: '男'
        },
        {
          value: 2,
          label: '女'
        }
      ],
      statusOption: [
        {
          value: 1,
          label: '已启用'
        },
        {
          value: 2,
          label: '已禁用'
        }
      ],
      labelListOption: {}, //标签集合
      ruleForm: {
        packageType: 2, //服务包类型
        packageName: '', //服务包名称
        limitAgeMin: '', //最小年龄
        limitAgeMax: '', //最大年龄
        limitSexType: 0, //限制性别
        serviceDays: '', //服务时长
        packageStatus: 1, //服务包状态
        originalPrice: '', //服务包价格
        discountPrice: '', //优惠价格
        labelIdList: [], //服务包标签id集合
        labelNameList: '', //服务包名字集合
        itemsJson: [] //服务项目集合
      },
      rules: {
        packageName: [{ required: true, message: '服务包名称不能为空', trigger: 'change' }],
        limitAgeMin: [{ type: 'number', validator: formRule.validateInteger, trigger: 'change' }],
        limitAgeMax: [
          { type: 'number', validator: formRule.validateInteger, trigger: 'change' },
          { validator: validateAge, trigger: 'change' }
        ],
        serviceDays: [
          { type: 'number', required: true, message: '服务时长不能为空', trigger: 'change' },
          { validator: formRule.validateInteger, trigger: 'change' }
        ],
        originalPrice: [{ type: 'number', validator: formRule.validateNumber, trigger: 'change' }],
        discountPrice: [
          { type: 'number', validator: formRule.validateNumber, trigger: 'change' },
          { validator: validatePrice, trigger: 'change' }
        ],
        labelIdList: [
          { type: 'array', required: true, message: '请选择服务标签', trigger: 'change' }
        ],
        itemsJson: [{ type: 'array', required: true, message: '请选择服务项目', trigger: 'change' }]
      }
    }
  },
  created() {
    this.getLabelist() //获取标签列表
    if (this.$route.query.id) {
      this.getservicePackageDetail() //获取服务包详情
    }
  },
  methods: {
    //触发验证
    handlervalidate(name) {
      this.$refs['ruleForm'].validateField(name)
    },
    //获取标签列表
    getLabelist() {
      this.$emit('viewLoad', true)
      this.$http
        .post(process.env.VUE_APP_API + '/family/label/getBy')
        .then(res => {
          if (res.code === 200) {
            let result = res.data || [] //标签列表
            this.labelListOption = {}
            for (let index in result) {
              this.labelListOption[result[index].labelId] = result[index].labelName
            }
          } else {
            this.$message.error(res.msg)
          }
          this.$emit('viewLoad', false)
        })
        .catch(error => {
          this.$emit('viewLoad', false)
          this.$message.error(error)
        })
    },
    //获取服务包详情
    getservicePackageDetail() {
      this.$http
        .get(process.env.VUE_APP_API + '/family/servicePackage/getDtoByPackageId', {
          packageId: this.$route.query.id
        })
        .then(res => {
          if (res.code === 200) {
            let result = res.data
            this.ruleForm.packageId = result.packageId
            for (let key in this.ruleForm) {
              this.ruleForm[key] = result[key]
            }
            this.ruleForm.itemsJson = result.itemsJson ? JSON.parse(result.itemsJson) : [] // 服务包项目
            this.ruleForm.labelIdList = result.labelIdList.split(',') //标签集合id
            this.$nextTick(() => {
              this.$refs['ruleForm'].clearValidate()
            })
          } else {
            this.$message.error(res.msg)
          }
        })
        .catch(error => {
          this.$message.error(error)
        })
    },
    //保存设置
    submitForm(formName) {
      this.$refs['ruleForm'].validate(valid => {
        if (valid) {
          this.$refs.serviceTable.$children[0].validate(svalid => {
            let params = {}
            let result = JSON.parse(JSON.stringify(this.ruleForm))
            params.itemsJson = JSON.stringify(result.itemsJson) //服务包项目
            params.labelIdList = result.labelIdList.join(',') //服务包标签
            params.labelNameList = result.labelIdList.map(el => this.labelListOption[el]).join(',')
            params = Object.assign({}, this.ruleForm, params)
            if (svalid) {
              if (this.$route.query.id) {
                this.editServePackage(params) //获取服务包详情
              } else {
                this.addServePackage(params)
              }
            }
          })
        }
      })
    },
    //添加服务包
    addServePackage(params) {
      this.$http
        .post(process.env.VUE_APP_API + '/family/servicePackage/insert', params)
        .then(res => {
          if (res.code === 200) {
            this.$message.success('添加成功')
            this.handleBackList() //返回列表
          } else {
            this.$message.error(res.msg)
          }
        })
        .catch(error => {
          this.$message.error(error)
        })
    },
    //编辑服务包
    editServePackage(params) {
      this.$http
        .post(process.env.VUE_APP_API + '/family/servicePackage/update', params)
        .then(res => {
          if (res.code === 200) {
            this.$message.success('修改成功')
            this.handleBackList() //返回列表
          } else {
            this.$message.error(res.msg)
          }
        })
        .catch(error => {
          this.$message.error(error)
        })
    },
    //返回列表
    handleBackList() {
      this.$router.push({
        path: '/system-manage/servicePackage-manage/list'
      })
    }
  }
}
</script>

<style lang="less">
.servicePackage-box {
  .inner-box {
    padding: 30px 70px;
    .input-box {
      width: 240px;
    }
    .small-box {
      width: 110px;
    }
    span.line {
      width: 20px;
      text-align: center;
    }
    span.label-text {
      margin-left: 10px;
    }
    .flex {
      > .el-form-item__content {
        display: flex;
      }
    }
    .el-form-item {
      &.is-success {
        .el-input__inner {
          border-color: #e3e0dc;
        }
      }
    }
  }
}
</style>
