<template>
  <div class="service-table-box">
    <el-form :model="model" :rules="model.rules" ref="ruleForm" >
      <table class="tables-box" cellspacing="0" cellspadding="0">
        <thead>
          <th><span>*</span>项目名称</th>
          <th>项目详情</th>
          <th><span>*</span>履约类型</th>
          <th><span>*</span>履约内容</th>
          <th><span>*</span>履约格式</th>
          <th>履约次数</th>
          <th>状态</th>
          <th>操作</th>
        </thead>
        <tbody v-for="(item,index) in model.tableList" :key="index">
            <tr v-if="index>0">
              <td colspan="8" class="title">项目{{index+1}}</td>
            </tr>
            <tr>
              <td>
                <el-form-item :prop="'tableList.' + index + '.itemName'" :rules='model.rules.itemName' :show-message="false">
                  <el-input v-model.trim="item.itemName" placeholder="请输入" clearable class="input-box"></el-input>
                </el-form-item>
              </td>
              <td>
                <el-input v-model.number="item.itemDetail" placeholder="请输入" clearable class="input-box"></el-input>
              </td>
              <td>
                <el-form-item :prop="'tableList.' + index + '.appointType'" :rules='model.rules.appointType' :show-message="false">
                  <el-select placeholder="请选择" v-model="item.appointType" clearable class="input-box" @change="appointTypeChange($event,item)">
                    <el-option v-for="(value,key) in appointTypeOption" :key="key" :label="value" :value="+key"></el-option>
                  </el-select>
                </el-form-item>
              </td>
              <td>
                <template v-if="Object.keys(formatAppointDetail(item.appointType)).length>0">
                  <el-form-item :prop="'tableList.' + index + '.appointDetail'" :rules='model.rules.appointDetail' :show-message="false">
                    <el-select placeholder="请选择" v-model="item.appointDetail" clearable class="input-box">
                      <el-option v-for="(value,key) in formatAppointDetail(item.appointType)" :key="key" :label="value" :value="key"></el-option>
                    </el-select>
                  </el-form-item>
                </template>
                <template v-else>{{EMPTYTEXT}}</template>
              </td>
              <td>
                <template v-if="item.appointType!==16">
                  <el-form-item :prop="'tableList.' + index + '.appointFormat'" :rules='model.rules.appointFormat' :show-message="false">
                    <el-select placeholder="请选择" v-model="item.appointFormat" clearable class="input-box">
                      <el-option v-for="(value,key) in appointFormatOption" :key="key" :label="value" :value="+key"></el-option>
                    </el-select>
                  </el-form-item>
                </template>
                <template v-else>{{EMPTYTEXT}}</template>
              </td>
              <td>
                <template v-if="item.appointType!==16">
                  <el-form-item :prop="'tableList.' + index + '.appointNum'" :rules='model.rules.appointNum' :show-message="false">
                    <el-input v-model.number="item.appointNum" placeholder="请输入数字" clearable class="input-box" maxlength="4"></el-input>
                  </el-form-item>
                </template>
                <template v-else>{{EMPTYTEXT}}</template>
              </td>
              <td>
                <template v-if="item.appointType!==16">
                  <el-select placeholder="请选择" v-model="item.itemStatus" clearable class="input-box"> 
                    <el-option v-for="(value,key) in statusOption" :key="key" :label="value" :value="+key"></el-option>
                  </el-select>
                </template>
                <template v-else>{{EMPTYTEXT}}</template>
              </td>
              <td>
                <el-button type="text" @click="deleteServiceItem(model.tableList,index)">删除</el-button>
                <el-button type="text" @click="addExecuteItem(item,index)" v-if="item.appointType===16">添加执行项</el-button>
              </td>
            </tr>
            <tr v-for="(it,i) in item.childrens" :key="i">
              <td colspan="8" class="subTable">
                <table cellspacing="0" cellspadding="0">
                  <tbody>
                    <tr>
                      <td colspan="8" class="title">执行项{{i+1}}</td>
                    </tr>
                    <tr>
                      <td>
                        <el-form-item :prop="'tableList.' + index+'.childrens.' + i +'.itemName'" :rules="model.rules.itemName" :show-message="false">
                          <el-input v-model.trim="it.itemName" placeholder="请输入" clearable class="input-box"></el-input>
                        </el-form-item>
                      </td>
                      <td>
                        <el-input v-model.number="it.itemDetail" placeholder="请输入" clearable class="input-box"></el-input>
                      </td>
                      <td>
                        <el-form-item :prop="'tableList.' + index+'.childrens.' + i +'.appointType'"  :rules="model.rules.appointType" :show-message="false">
                          <el-select placeholder="请选择" v-model="it.appointType" clearable class="input-box">
                            <el-option v-for="(value,key) in formatAppointType(item.appointType)" :key="key" :label="value" :value="+key"></el-option>
                          </el-select>
                        </el-form-item>
                      </td>
                      <td>
                         <template v-if="Object.keys(formatAppointDetail(it.appointType)).length>0">
                          <el-form-item :prop="'tableList.' + index+'.childrens.' + i +'.appointDetail'"  :rules="model.rules.appointDetail" :show-message="false">
                            <el-select placeholder="请选择" v-model="it.appointDetail" clearable class="input-box">
                              <el-option v-for="(value,key) in formatAppointDetail(it.appointType)" :key="key" :label="value" :value="key"></el-option>
                            </el-select>
                          </el-form-item>
                        </template>
                        <template v-else>{{EMPTYTEXT}}</template>
                      </td>
                      <td>
                        <el-form-item :prop="'tableList.' + index+'.childrens.' + i +'.appointFormat'"  :rules="model.rules.appointFormat" :show-message="false">
                          <el-select placeholder="请选择" v-model="it.appointFormat" clearable class="input-box">
                            <el-option v-for="(value,key) in appointFormatOption" :key="key" :label="value" :value="+key"></el-option>
                          </el-select>
                        </el-form-item>
                      </td>
                      <td>
                        <el-form-item :prop="'tableList.' + index+'.childrens.' + i +'.appointNum'"  :rules="model.rules.appointNum" :show-message="false">
                          <el-input v-model.number="it.appointNum" placeholder="请输入数字" clearable class="input-box" maxlength="4"></el-input>
                        </el-form-item>
                      </td>
                      <td>
                        <el-select placeholder="请选择" v-model="it.itemStatus" clearable class="input-box">
                          <el-option v-for="(value,key) in statusOption" :key="key" :label="value" :value="+key"></el-option>
                        </el-select>
                      </td>
                      <td>
                        <el-button type="text" @click="deleteExecuteItem(item.childrens,i)">删除</el-button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </td>
            </tr>
        </tbody>
      </table>
    </el-form>
    <!-- 添加项目 -->
    <div class="add-box">
      <el-button type="text" icon="el-icon-circle-plus" @click="addServiceItem">添加项目</el-button>
    </div>
  </div>
</template>

<script>
import formRule from '../../../util/formRule' //表单验证
export default {
    name:'service-table',
    props: {
      dataInfo:{
        type:Array,
        default:()=>[]
      }
    },
    data() {
      return {
        appointTypeOption:{
          1:'测量类',
          2:'标记类',
          4:'公共卫生类',
          8:"指导类",
          16:"组合类"
        },//履约类型
       measureOption: {
        109: '心率',
        110: '心电',
        111: '血氧',
        112: '血压',
        113: '体温',
        114: '血糖',
        115: '尿酸',
        116: '尿常规',
        117: '血红蛋白',
        118: '血脂',
        119: 'BMI'
      }, //测量类履约内容
        publicHealthOption:{
          2001:'健康档案',
          2002:'健康体检',
          2003:'预防接种',
          2004:"儿童",
          2005:"孕产妇",
          2006:'老年人',
          2007:'高血压',
          2008:'糖尿病',
          2009:"严重精神障碍",
          2010:"肺结核",
          2011:'健康教育',
          2012:'信息报告',
          2013:'巡查登记',
          2014:"传染病报告",
          2015:"突发公共卫生事件"
        },//公共卫生类履约内容
        appointFormatOption:{
          1:'年',
          2:'季度',
          4:'月',
          8:'日'
        },//履约格式
        statusOption:{
          1:'是',
          2:'否',
        }, //状态
        model:{
          rules:{
            itemName: [
              { required:true,message:'请输入项目名称', trigger: 'change' }
            ],
            appointType: [
              { required:true,message:'请选择', trigger: 'change' }
            ],
            appointDetail: [
              { required:true,message:'请选择', trigger: 'change' }
            ],
            appointDetail: [
              { required:true,message:'请选择', trigger: 'change' }
            ],
            appointFormat:[
              { required:true,message:'请选择', trigger: 'change' }
            ],
            appointNum:[
              {validator:formRule.validateInteger, trigger: 'change' }
            ]
          },
          tableList:this.dataInfo
        }
      };
    },
    watch:{
      dataInfo(n,o){
        this.model.tableList = n
      }
    },
    created() {
    },
    methods: {
      //初始化履约内容
      formatAppointDetail(val){
        let result = {}
        if(val==1){
          result = Object.assign({},this.measureOption)
        }else if(val==4){
          result = Object.assign({},this.publicHealthOption)
        }else{
          result = {}
        }
        return result
      },
      //初始化履约类型
      formatAppointType(parentType){
        let result = {}
        if(parentType==16){
          let params = JSON.parse(JSON.stringify(this.appointTypeOption))
          delete params[parentType]
          result = Object.assign({},params)
        }else{
          result = this.appointTypeOption
        }
        return result
      },
      //添加服务项目
      addServiceItem(){
        let obj = {
          itemName:"",
          itemDetail:"",
          appointType:'',
          appointFormat:'',
          appointNum:'',
          appointDetail:"",
          itemStatus:1,
          childrens:[]
        }
        this.dataInfo.push(obj)
      },
      //添加执行项
      addExecuteItem(item){
        let obj = {
          itemName:"",
          itemDetail:"",
          appointType:'',
          appointFormat:'',
          appointNum:'',
          appointDetail:"",
          itemStatus:1
        }
        item.childrens.push(obj)
      },
      //删除服务
      deleteServiceItem(item,index){
        if(item[0].childrens.length>0){
          this.$confirm('确定后，子项也将一并删除', '提示', {
            confirmButtonText: '确 认',
            cancelButtonText: '取 消',
            customClass:'infoMessage',
            center:true
          }).then(() => {
            item.splice(index,1)
          })
        }else{
          item.splice(index,1)
        }
      },
      //删除项目
      deleteExecuteItem(item,i){
        item.splice(i,1)
      },
      //履约类型改变
      appointTypeChange(val,item){
        item.appointDetail = ''
        if(item.childrens.length>0&&val!=16){
          this.$confirm('修改后，子项也将一并删除', '提示', {
            confirmButtonText: '确 认',
            cancelButtonText: '取 消',
            customClass:'infoMessage',
            center:true
          }).then(() => {
            item.childrens = []
          }).catch(() => {
            item.appointType=16
          });
        }
      }
    }
};
</script>

<style lang="scss">
@import "./tables";
.service-table-box{
  @include tables-box();
  .add-box{
    border:1px solid #f4ebe3;
    border-top:none;
    line-height:60px;
    text-align:center;
    .el-button{
      font-size: 16px;
      color: #318bfe;
    }
  }
}


</style>
